.promo-video {
  position: relative;
  min-height: 706px;
  margin-bottom: 248px;
  background-color: $dark-black;

  @media (max-width: $desktop-width-only) {
    margin-bottom: 17.45%;
  }

  @media (max-width: $tablet-width-only) {
    min-height: 413px;
    margin-bottom: 20.64%;
  }

  @media (max-width: $mobile-width-only) {
    min-height: 384px;
    margin-bottom: 38.28%;
  }
}

.promo-video img {
  width: 100%;
  height: auto;
}

.promo-video button {
  position: absolute;
  top: 49.5%;
  bottom: 50%;
  left: 50%;
  height: 65px;
  width: 65px;
  margin-left: -32.5px;
  margin-top: -32.5px;
  border: none;
  background-color: transparent;
  cursor: pointer;

  &::before,
  &::after {
    content: "";
    position: absolute;
  }

  &::before {
    top: 0;
    left: 0;
    width: 65px;
    height: 65px;
    background-color: $light-blue;
    border-radius: 50%;
  }

  &::after {
    top: 23.5px;
    left: 28.6px;
    border: 9px solid transparent;
    border-right: none;
    border-left: 13px solid $another-type-of-black;
  }

  @media (max-width: $tablet-width-only) {
    top: 45%;
    height: 90.3px;
    width: 90.3px;
    margin-left: -45.2px;

    &::before {
      width: 90.3px;
      height: 90.3px;
    }

    &::after {
      top: 32.5px;
      left: 39.7px;
      border: 12px solid transparent;
      border-left: 18px solid $another-type-of-black;
    }
  }

  @media (max-width: $mobile-width-only) {
    margin-left: -33px;
    height: 65px;
    width: 65px;

    &::before {
      height: 65px;
      width: 65px;
    }

    &::after {
      top: 23.5px;
      left: 28.6px;
      border: 9px solid transparent;
      border-left: 13px solid $another-type-of-black;
    }
  }
}

.promo-video .container {
  padding-top: 120px;

  @media (max-width: $tablet-width-only) {
    padding-top: 70px;
  }

  @media (max-width: $mobile-width-only) {
    padding-top: 84px;
  }
}

.promo-video h2 {
  width: 350px;
  margin: 0 auto;
  margin-bottom: 20px;
  letter-spacing: -0.15px;

  @include fstyle(30px, 30px, $white, 600);

  @media (max-width: $desktop-width-only) {
    width: 42.9%;
    text-align: center;
  }

  @media (max-width: $tablet-width-only) {
    width: 280px;
    letter-spacing: -0.12px;

    @include fstyle(24px, 30px, $white, 600);
  }

  @media (max-width: $mobile-width-only) {
    margin-bottom: 19px;
  }
}

.promo-video p {
  width: 340px;
  margin: 0 auto;
  text-align: center;

  @include fstyle(16px, 28px, rgba($white, 0.7), 300);

  @media (max-width: $desktop-width-only) {
    width: 38%;
    text-align: center;
  }

  @media (max-width: $tablet-width-only) {
    width: 55.8vw;
    letter-spacing: -0.1px;
  }

  @media (max-width: $mobile-width-only) {
    width: 90vw;
  }
}

.bicycles-video__wrapper {
  position: absolute;
  top: 282px;
  left: 50%;
  margin-left: -456px;

  @media (max-width: $desktop-width-only) {
    top: 39.9%;
    left: 50vw;
    height: auto;

    @include imageWidth(64.5%);
  }

  @media (max-width: $tablet-width-only) {
    top: 216px;

    @include imageWidth(67%);
  }

  @media (max-width: $mobile-width-only) {
    top: 271px;

    @include imageWidth(90%);
  }
}
